.wrapBasic #header {
  display: flex;
  width: $widhtSite;
  height: $heightHead;
  position: relative;
  @include mobile(){
    width: 100%;
  }
}
.wrapBasic {
  header { // шапка
    display: flex;
    justify-content: center;
    align-items: center;
    height: $heightHead;
    width: 100%;
    position: fixed;
    z-index: 100;
    background-color: #fff;
    border-bottom: 1px solid #cccccc;
  }
}



