.footer {
  margin-top: 50px;
  background-color: #444444;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 120px;
  width: 100%;
  position: relative;
  &__social {
    display: flex;
    justify-content: center;
    align-items: center;
    @include mobile(){
      margin: 30px auto;
    }
    &>div {
      position: relative;
    }
    img {
      width: 25px;
      opacity: 0.6;
      transition: all 0.2s;
      &:hover {
        opacity: 1;
      }
    }
    a {
      position: relative;
    }
    &--instagram {
      margin-right: 25px;
    }
    &--facebook {

    }
    &--youtube {
      margin-right: 25px;
    }
  }
  &__form {
    display: flex;
    justify-content: center;
  }
  &__wrap {
    width: 1100px;
    @include mobile(){
      width: 100%;
    }
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    @include mobile(){
      flex-direction: column;
    }
  }
}


.footer {
  &__form {
    form {
      display: flex;
      width: 100%;
      @include mobile(){
        flex-direction: column;
        justify-content: center;
      }
    }
    input {
      width: 230px;
      height: 40px;
      margin: 0;
      padding: 0 0 0 2%;
      font-family: 'Roboto', sans-serif;
      font-size: 16px;
      font-weight: 400;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      border: 1px solid #ffffff;
      outline: none;
      @include mobile() {
        width: 88%;
        margin: 20px auto 10px;
      }
    }
    .sendFormButton {
      width: 156px;
      height: 40px;
      font-family: 'Roboto', sans-serif;
      font-size: 16px;
      background-color: $colorFormButtonHover;
      color: $colorFontInputButton;
      transition: all 0.2s;
      cursor: pointer;
      border: 1px solid $colorFormButtonHover;
      outline: none;
      display: flex;
      justify-content: center;
      align-items: center;
      @include mobile(){
        width: 90%;
        margin: 0 auto;
      }
      &:hover {
        opacity: 0.8;
      }
    }
  }
}
/* error*/

.footer {
  &__error {
    position: absolute;
    top: -65px;
    width: 400px;
    display: block;
    justify-content: center;
    align-items: center;
    background: #ffffff;
    box-shadow: 0 0 7px #777777;
    border-radius: 3px;
    @include mobile() {
      width: 100%;
      border-radius: 0;
    }
    img {
      width: 30px;
      display: block;
      position: relative;
      top: 5px;
      left: 5px;
      @include mobile() {
        margin: 10px auto;
        top: 0;
        left: 0;
      }
    }
    p {
      font-family: 'Roboto', sans-serif;
      color: #3399ff;
      font-size: 17px;
      padding-left: 20px;
      padding-bottom: 20px;
      font-weight: bold;
      text-align: center;
      @include mobile(){
        padding-left: 0;
      }
    }
  }
}