.topBlockWrap {
  background-color: $colorMainSite;
  height: 100vh;
  position: relative;
}
#b0 {
  .form-blockB1 {
    position: absolute;
    bottom: 0;
    height: 30%;
    z-index: 200;
    background: none;
  }

  .wrapBlock__content {
    width: 1100px;
    margin: 0 auto;
  }
  .wrapBlock__text {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 6%;
    top: 35%;
    text-align: center;
    padding: 0;
    font-family: 'Roboto Slab', serif;
    color: #ffffff;
    h1 {
      font-size: 28px;
      padding-top: 10px;
    }
    h2 {
      font-size: 30px;
      font-weight: bold;
      padding-top: 10px;
      color: #ffcc00;
      transition: all 0.3s;
    }
    h3 {
      font-size: 28px;
      font-weight: bold;
      color: #ffffff;
      padding-top: 26px;

    }
    h4 {
      font-size: 30px;
      font-weight: bold;
      color: #ffcc00;
    }
  }
}
#b0-fon {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../img/b180/avtotur.jpg");
  background-position: center center;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  z-index: 100;
}

