#b180 {
  position: absolute;
  height: 100%;
  top: 0;
  right: 13%;
  width: 20%;
  span {
    display: block;
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 200;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ffffff+42,4bb88c+73&0+42,1+78,1+78 */
    background: -moz-linear-gradient(top,  rgba(255,255,255,0) 42%, rgba(75,184,140,0.86) 73%, rgba(75,184,140,1) 78%); /* FF3.6-15 */
    background: -webkit-linear-gradient(top,  rgba(255,255,255,0) 42%,rgba(75,184,140,0.86) 73%,rgba(75,184,140,1) 78%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom,  rgba(255,255,255,0) 42%,rgba(75,184,140,0.86) 73%,rgba(75,184,140,1) 78%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#4bb88c',GradientType=0 ); /* IE6-9 */

  }
  img {
    position: relative;
    top: 35%;
    //box-shadow: 0 0 7px #333333;
    z-index: 100;
    width: 100%;
    border-top: 1px solid #cccccc;
  }
}