.reviews {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  position: relative;
  h2 {
      text-align: center;
      font-family: Roboto, 'sans-serif';
      font-size: 30px;
      width: 100%;
      color: #333333;
      padding-top: 20px;
      text-transform: capitalize;
  }
  .rivewsBlock {
    width: 90%;
    margin: 0 auto;
    button {
      margin: 0;
      padding: 0;
    }
    &__one {
      text-align: center;
      &--text {
        font-family: 'Roboto Slab', sans-serif;
        font-size: 25px;
        color: #333333;
        font-weight: normal;
        width: 70%;
        margin: 0 auto;
        text-align: center;
        padding-top: 40px;
      }
      &--face {
        width: 80px;
        border-radius: 50%;
        margin: 25px auto;
      }
      &--name {
        font-size: 18px;
        color: #444444;
        font-weight: 700;
        padding-bottom: 10px;
      }
      &--prof {
        font-size: 18px;
        color: #444444;
        font-weight: 400;
      }
      &--arrowPrev {
        width: 40px;
        height: 40px;;
        border: none;
        background: none;
        cursor: pointer;
        outline: none;
        position: absolute;
        cursor: pointer;
        left: 0;
        top: 50%;
        z-index: 80;
        @include mobile(){
          display: none;
        }
        img {
          width: 40px;
          height: 40px;;
          outline: none;
          opacity: 0.6;
          transition: all 0.2s;
          &:hover {
            opacity: 1;
          }
        }
      }
      &--arrowNext {
        width: 40px;
        height: 40px;
        border: none;
        background: none;
        cursor: pointer;
        outline: none;
        position: absolute;
        right: 0;
        top: 50%;
        @include mobile(){
          display: none;
        }
        img {
          width: 40px;
          height: 40px;
          outline: none;
          transform: rotate(180deg);
          opacity: 0.6;
          transition: all 0.2s;
          &:hover {
            opacity: 1;
          }
        }
      }
    }
  }
}
.form-blockB1 {
  background-color: $colorMainSite;
  input {
    border: none;
  }
}