.wrapBasic {
  .wrapSite { // обложка
    width: 100%;
    padding-top: 0;
    margin: 0 auto;
    background-color: #ffffff;
    @include mobile() {
      width: 100%;
    }
    .lineTop {
      border-top: 1px solid #cccccc;
    }
    .wrapBlock {
      width: 100%;
      &__content { // обложка блоков
        display: flex;
        min-height: 400px;
        width: $widhtSite;
        position: relative;
        z-index: 200;
        @include mobile(){
          width: 100%;
        }
        &:first-child {
          margin: 0 auto;
        }
      }
      &__text {
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      &__img {
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
}