.wrapBasic {
  header { // шапк
    nav { // навигация
      flex-grow: 1;
      display: flex;
      justify-content: flex-end;
      @include mobile() {
        position: fixed;
        width: 100%;
        top: 70px;
        display: none;
        background-color: #ffffff;
        border-top: 1px solid #cccccc;
        border-bottom: 1px solid #cccccc;
      }
      ul { // списко навигации
        display: flex;
        @include mobile(){
          flex-direction: column;
          width: 100%;
          padding-top: 20px;
          background-color: #f3f3f3;
        }
        li {
          cursor: pointer;
          display: flex;
          justify-content: center;
          align-items: center;
          height: $heightHead;
          position: relative;
          flex-direction: column;
          padding: 0 25px;
          font-size: 16px;
          color: #333333;
          &:hover {
            color: #444444;
            @include mobile(){
              color: #000000;
            }
          }
          b {
            display: block;
            position: relative;
            top: -43px;
            left: 0;
            width: 100%;
            height: 3px;
            opacity: 0;
            transition: all 0.2s;
            background-color: #cccccc;
            padding: 0 0 0 40px;
            @include mobile() {
              height: 100%;
              top: -37px;
            }
          }
        }
      }
    }

  }
}

// адаптивность




.container {
  width: 100%;
  margin: 0 auto;
  display: none;
  justify-content: space-between;
  @include mobile() {
    display: flex;
  }
}
.container input {
  display: none;
}

.hamburger {
  margin: 0 auto;
  width: 24px;
  height: 22px;
  position: fixed;
  z-index: 400;
  top: 24px;
  right: 5%;
}

.hamburger .bar {
  padding: 0;
  width: 24px;
  height: 4px;
  background-color: #4bb88c;
  display: block;
  border-radius: 4px;
  transition: all 0.4s ease-in-out;
  position: absolute;
}

.bar1 {
  top: 0;
}

.bar2,
.bar3 {
  top: 9px;
}

.bar3 {
  right: 0;
}

.bar4 {
  bottom: 0;
}

/* HAMBURGER 1 */
.checkbox1:checked + label > .hamburger1 > .bar1{
  transform: rotate(45deg);
  transform-origin: 5%;
  width: 28px
}

.checkbox1:checked + label > .hamburger1 > .bar2 {
  transform: translateX(-40px);
  background-color: transparent;
}

.checkbox1:checked + label > .hamburger1 > .bar3 {
  transform: translateX(40px);
  background-color: transparent;
}

.checkbox1:checked + label > .hamburger1 > .bar4 {
  transform-origin: 5%;
  transform: rotate(-45deg);
  width: 28px;
}
