.wrapBlock {
  z-index: 1;
  &__form {
    padding: 40px 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    form {
      display: flex;
      width: 100%;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      z-index: 500;
    }
    @include mobile() {
      height: auto;
      flex-direction: column;
    }
    input {
      width: 230px;
      padding: 10px;
      margin: 0 20px;
      font-family: 'Roboto', sans-serif;
      font-size: 18px;
      font-weight: 400;
      border-radius: 3px;
      border: 2px solid $colorFormButton;
      z-index: 101;
      box-shadow: 0 0 7px #b2b2b2;
      @include mobile() {
        padding: 10px 0 10px 2%;
        width: 100%;
        margin: 0 0 20px 0;

      }
    }
    button {
      width: 200px;
      padding: 13px 10px;
      margin-top: 35px;
      font-family: 'Roboto', sans-serif;
      font-size: 19px;
      background-color: $colorFormButton;
      border: none;
      border-radius: 3px;
      color: $colorFontInputButton;
      box-shadow: 0 3px 3px $colorBoxShadow;
      transition: all 0.2s;
      cursor: pointer;
      outline: none;
      @include mobile(){
        margin-top: 5px;
        width: 90%;
      }
      &:hover {
        background-color: $colorFormButtonHover;
        color: $colorFormButtonHoverText;
      }
    }
  }
  &__blockInput {
    width: 100%;
    display: flex;
    position: relative;
    justify-content: center;
    @include mobile() {
      flex-direction: column;
      width: 86%;
      position: relative;
      left: -1%;
    }
  }
}
/* error*/

.wrapBlock {
  &__error {
    position: absolute;
    top: -35px;
    width: 280px;
    height: 70px;
    display: none;
    justify-content: center;
    align-items: center;
    background: rgba(97, 90, 90, 0.8);
    box-shadow: 0 0 7px #777777;
    border-radius: 3px;
    @include mobile() {
      width: 100%;
      border-radius: 0;
    }
    p {
      font-family: 'Roboto', sans-serif;
      color: #ffffff;
      font-size: 18px;
      font-weight: normal;
      display: flex;
      position: absolute;
      width: 100%;
      height: 100%;
      justify-content: center;
      align-items: center;
      @include mobile(){
        padding-left: 0;
      }
    }
  }
}

/* animation form ajax */
.floatingBarsG{
  position:relative;
  width:19px;
  height:23px;
  margin:auto;
  display: none;
}

.blockG{
  position:absolute;
  background-color:rgb(255,255,255);
  width:3px;
  height:7px;
  border-radius:2px 2px 0 0;
  -o-border-radius:2px 2px 0 0;
  -ms-border-radius:2px 2px 0 0;
  -webkit-border-radius:2px 2px 0 0;
  -moz-border-radius:2px 2px 0 0;
  transform:scale(0.4);
  -o-transform:scale(0.4);
  -ms-transform:scale(0.4);
  -webkit-transform:scale(0.4);
  -moz-transform:scale(0.4);
  animation-name:fadeG;
  -o-animation-name:fadeG;
  -ms-animation-name:fadeG;
  -webkit-animation-name:fadeG;
  -moz-animation-name:fadeG;
  animation-duration:0.572s;
  -o-animation-duration:0.572s;
  -ms-animation-duration:0.572s;
  -webkit-animation-duration:0.572s;
  -moz-animation-duration:0.572s;
  animation-iteration-count:infinite;
  -o-animation-iteration-count:infinite;
  -ms-animation-iteration-count:infinite;
  -webkit-animation-iteration-count:infinite;
  -moz-animation-iteration-count:infinite;
  animation-direction:normal;
  -o-animation-direction:normal;
  -ms-animation-direction:normal;
  -webkit-animation-direction:normal;
  -moz-animation-direction:normal;
}

.rotateG_01{
  left:0;
  top:8px;
  animation-delay:0.2095s;
  -o-animation-delay:0.2095s;
  -ms-animation-delay:0.2095s;
  -webkit-animation-delay:0.2095s;
  -moz-animation-delay:0.2095s;
  transform:rotate(-90deg);
  -o-transform:rotate(-90deg);
  -ms-transform:rotate(-90deg);
  -webkit-transform:rotate(-90deg);
  -moz-transform:rotate(-90deg);
}

