.infoFormOk {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(51, 51, 51, 0.5);
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
  &__content {
    background: #ffffff;
    padding: 30px 50px;
    border-radius: 3px;
    box-shadow: 0 0 7px #777777;
    display: flex;
    justify-content: center;
    flex-direction: column;
    @include mobile(){
      text-align: center;
      padding: 30px 5%;
    }
    p {
      color: #3399ff;
      font-weight: bold;
      font-size: 17px;
      font-family: 'Roboto Slab', serif;
    }
    button {
      margin-top: 25px;
      display: inline-block;
      padding: 10px 15px;
      background-color: rgba(51, 153, 225, 1);
      color: #ffffff;
      border: none;
      border-radius: 2px;
      cursor: pointer;
      transition: all 0.2s;
      box-shadow: 0 5px 5px #777777;
      font-weight: bold;
      font-family: 'Roboto Slab', serif;
      font-size: 16px;
      &:hover {
        background-color: #66ff33;
      }
    }
  }
}